// lib/supabase/middleware.ts
import { createSupabaseServerClient } from "./client"; // <-- yangi nom
import { NextRequest, NextResponse } from "next/server";

export async function updateSession(request: NextRequest) {
  let response = NextResponse.next();

  const supabase = await createSupabaseServerClient(request);

  let user = null;
  try {
    const {
      data: { user: gotUser },
    } = await supabase.auth.getUser();
    user = gotUser;
    console.log("[middleware] supabase.getUser ->", Boolean(user));
  } catch (e) {
    console.error("[middleware] supabase.auth.getUser failed:", e);
  }

  const protectedPaths = ["/chat", "/settings", "/account"];
  const isProtectedPath = protectedPaths.some((p) =>
    request.nextUrl.pathname.startsWith(p)
  );
  if (isProtectedPath && !user) {
    const url = request.nextUrl.clone();
    url.pathname = "/login";
    return NextResponse.redirect(url);
  }

  const authPaths = ["/login", "/signup"];
  const isAuthPath = authPaths.some((p) =>
    request.nextUrl.pathname.startsWith(p)
  );
  if (isAuthPath && user) {
    const url = request.nextUrl.clone();
    url.pathname = "/chat";
    return NextResponse.redirect(url);
  }

  return response;
}
